.BillHeadInfoWrap {
  display: inline-block;
}
.BillHeadInfoWrap .bill-info-title,
.BillHeadInfoWrap .bill-info-code {
  font-weight: normal;
  font-size: 16px;
  height: 32px;
  line-height: 32px;
  color: #111;
  vertical-align: super;
  margin-right: 8px;
}
.BillHeadInfoWrap .bill-info-title {
  font-weight: bold;
}
.BillHeadInfoWrap .title-font-size-medium {
  font-size: 18px !important;
}
.BillHeadInfoWrap .title-font-size-large {
  font-size: 20px !important;
}
.BillHeadInfoWrap .page-title-Icon {
  margin: 4px 10px 0 0;
}
.BillHeadInfoWrap.BillHeadInfoWrap-showBackBtn div[tabindex="-1"] {
  float: left;
  margin-top: 4px;
}
.BillHeadInfoWrap.BillHeadInfoWrap-showBackBtn .page-title-Icon {
  margin-left: 11px;
}
.BillHeadInfoWrap.BillHeadInfoWrap-showBackBtn .bill-info-title,
.BillHeadInfoWrap.BillHeadInfoWrap-showBackBtn .bill-info-code {
  margin-right: 8px;
  margin-left: 0px;
}
