.print-output-wrapper .u-modal-dialog {
  height: auto !important;
}
.print-output-wrapper .u-modal-body {
  height: 280px;
}
.print-output-wrapper .print-template-wrapper {
  border-bottom: none;
}
.print-output-wrapper .print-template-wrapper .header {
  border-bottom: none;
}
.print-output-wrapper .u-radio-group .u-radio {
  width: auto;
  height: 22px;
}
.print-output-wrapper .label,
.print-output-wrapper .in-form {
  display: inline-block;
  vertical-align: middle;
}
.print-output-wrapper .in-form {
  width: 300px;
}
.print-output-wrapper .label {
  width: 80px;
}
.print-output-wrapper .form-region {
  margin-top: 10px;
}
.print-output-wrapper .input-number-wrapper {
  display: block;
}
.print-output-wrapper .task-container {
  padding: 5px 5px 5px 20px;
}
.print-output-wrapper .task-container h3 {
  margin-bottom: 8px;
  line-height: 1.5;
  font-size: 13px;
  border-bottom: 1px solid #ddd;
}
.print-template-wrapper {
  border-bottom: none;
}
.print-template-wrapper .header {
  border-bottom: none;
}
.print-output-template-wrapper .print-choice {
  border-bottom: none !important;
  height: 46px;
  padding: 0px 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  width: 100%;
}
.print-output-template-wrapper .print-out-select {
  width: 200px;
  margin-right: 6px;
}
