.print-out-select {
  width: 200px;
  margin-right: 6px;
}
.print-output-contain .u-modal-body {
  height: 280px;
}
.print-template-wrapper .u-radio-group .u-radio {
  width: auto;
  height: 22px;
}
.print-template-wrapper .label,
.print-template-wrapper .in-form {
  display: inline-block;
  vertical-align: middle;
}
.print-template-wrapper .in-form {
  width: 300px;
}
.print-template-wrapper .label {
  width: 80px;
}
.print-template-wrapper .form-region {
  margin-top: 10px;
}
.print-template-wrapper .input-number-wrapper {
  display: block;
}
.print-template-wrapper .task-container {
  padding: 5px 5px 5px 20px;
}
.print-template-wrapper .task-container h3 {
  margin-bottom: 8px;
  line-height: 1.5;
  font-size: 13px;
  border-bottom: 1px solid #ddd;
}
